.home {
	padding: 56px 20px;
	min-height: 100%;
	width: 100%;
}

#pricing{
	background: #94AFCC;
	display: flex;
	flex:1;
	width: 100%;
	height: 100%;
	justify-content: center;
	align-items: center;
}

#pricing a{
	text-decoration: none;
	color: white;
}

.containerbox{
	background: transparent;
	width: 690px;
	height: 760px;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	perspective: 1000px;
}

.imagebox{
	position:absolute;
	z-index:1;
	margin-left: -185px;
}

.dropimage{
	background-image: url("../../assets/dropback.svg");
	background-repeat: no-repeat;
	width: 301px;
	height: 758px;	
	
}

.roundedtop{
	background: white;
	width:  670px;
	height: 100%;
	display: flex;
	flex-direction: row;
	flex: 1;
	border-radius:35px 35px 0px 0px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);

}

.lefttop{
	width: 173px;
	height: 100%;
	
}	


.righttop{
	display: flex;
	flex: 1;
	height: 100%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}


.leftbottom{
	width: 340px;
	height: 100%;
	justify-content: center;
	align-items: center;
	display: flex;
	padding: 10px;
}	


.rightbottom{
	display: flex;
	flex: 1;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.fullbottom{
	display: flex;
	flex: 1;
	align-items: center;
	justify-content: center;
}

.topcenter{
	display: flex;
	flex: 1;
	flex-direction: row;
	
}

.centerright{
	display: flex;
	flex: 0 0 auto;
	width: 250px;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.erroricon{
	padding:20px;
	width: 20px;
}

.centerleft{
	display: flex;
	flex: 1;
	height: 100%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.label{
	font-size:40px;
	color: white;
}

.smallabel{
	font-size:18px;
	color: rgba(255,255,255,0.6);
}

.centerbox{
	
	width: 690px;
	height: 337px;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	transition: 0.6s;
	transform-style: preserve-3d;
	position: relative;
}

.containerbox.flip .centerbox{
	transform: rotateY(180deg);
	
}

.front, .back {
	backface-visibility: hidden;
	position: absolute;
	top: 0;
	left: 0;
}

.front{
	width: 690px;
	height: 337px;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	position: absolute;
	
	/* for firefox 31 */
	transform: rotateY(0deg);
	background: #445662;
}

.back{
	width: 690px;
	height: 337px;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	position: absolute;
	z-index: 2;
	/* for firefox 31 */
	position: absolute;
	transform: rotateY(180deg);
	background: #445662;
}

.back2{
	width: 690px;
	height: 337px;
	display: flex;
	flex: 0 0 auto;
	flex-direction: column;
	position: absolute;
	z-index: 3;
	/* for firefox 31 */
	position: absolute;
	transform: rotateY(0deg);
	background: #445662;
}

.bottomcenter{
	display: flex;
	flex: 1;
	height: 100%;
	flex-direction: row;

}

.button {
	border-radius:20px 20px 20px 20px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
	padding:10px 20px 10px 20px;
	background: #E82B89;
	opacity: 0;
	animation: slide 0.5s forwards;
	justify-content: center;
	align-items:center;
	height: 80px;
}

.buttontext{
	color: white;
	font-size: 40px;
	font-weight: 700;
	user-select: none; /* supported by Chrome and Opera */
	-webkit-user-select: none; /* Safari */
	-khtml-user-select: none; /* Konqueror HTML */
	-moz-user-select: none; /* Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
}

@keyframes slide {
    100% { opacity: 1.0; }
}

.logo{
	background-image: url("../../assets/logo.svg");
	background-repeat: no-repeat;
	background-size: contain;
	width: 421px;
	height: 135px;
	display: flex;
}


.input{
	border: none;
	background: white;
	border-radius: 5px;
	height: 60px;
	width: 150px;
	color: #445662;
	font-size: 34px;
	padding-left: 10px;
}


.roundedbottom{
	background: white;
	width:  670px;
	height: 100%;
	display: flex;
	flex: 1;
	border-radius:0px 0px 35px 35px;
	box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);

}


.footertext{
	font-size: 25px;
	text-align: center;
    line-height: 130%;
}


