
.center{
	display: flex;
	flex: 1;
	flex-direction: column;
	
}

.title{
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #72838E;
}

.titletext{
    color: white;
    font-size: 20px;
}

.centercontent{
    display: flex;
	flex: 1;
	flex-direction: row;
}

.centerleft{
	display: flex;
	flex: 0 0 auto;
    width: 345px;
	flex-direction: column;
	
}

.centerright{
    display: flex;
	flex: 0 0 auto;
    width: 345px;
    flex-direction: column;
    
    border-left: 1px solid #72838E;
}

.label{
	font-size:46px;
    color: white;
    text-align: center
}

.quotedetails{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    padding:10px;
}

.timeframe{
    display: flex;
    flex: 0 0 auto;
    height: 60px;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.smallabel{
	font-size:18px;
	color: rgba(255,255,255,0.6);
}


.droplet{
	background-image: url("../../assets/droplet.svg");
	background-repeat: no-repeat;
	background-size: contain;
	width: 232px;
	height: 299px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.quote{
    font-size:50px;
	color: white;
}


.inputcell{
    display: flex;
    flex: 0 0 auto;
    height: 50px;
    padding-left: 15px;
    flex-direction: row;
}

.label{
    color: white;
    height: 30px;
    font-size: 20px;
    margin-right: 10px;
    flex: 0 0 auto;
    width: 150px;
    text-align: start;
}

.inputline{
    height: 30px;
}

.input{
    border-radius: 5px;
    border: 2px solid white;
    height: 30px;
    background-color: #72838E;
    padding-left:7px;
    color: white;
    font-weight: 700;
}

.inputlong{
    border-radius: 5px;
    border: 2px solid white;
    height: 30px;
    width: 160px;
    background-color: #72838E;
    padding-left:7px;
    color: white;
    font-weight: 700;
}

.inputshort{
    border-radius: 5px;
    border: 2px solid white;
    height: 30px;
    width: 80px;
    background-color: #72838E;
    padding-left:7px;
    color: white;
    font-weight: 700;
}

.detailtitle{
    display: flex;
    flex: 0 0 auto;
    justify-content: flex-start;
    align-items: flex-start;
    margin:15px 0px 30px 15px;
}

.detailtext{
    color: white;
    font-size: 12px;
    font-weight: 700;
}


