
.center{
	display: flex;
	flex: 1;
	flex-direction: row;
	
}

.centerleft{
	display: flex;
	flex: 0 0 auto;
    width: 300px;
    height: 337px;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.centerright{
	display: flex;
	flex: 1;
	height: 100%;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.label{
	font-size:46px;
    color: white;
    text-align: center
}

.quotedetails{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
    padding:10px;
}

.timeframe{
    display: flex;
    flex: 0 0 auto;
    height: 60px;
    align-items: center;
    justify-content: center;
    padding: 10px;
}

.smallabel{
	font-size:18px;
	color: rgba(255,255,255,0.6);
}


.droplet{
	background-image: url("../../assets/droplet.svg");
	background-repeat: no-repeat;
	background-size: contain;
	width: 232px;
	height: 299px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.quote{
    font-size:50px;
	color: white;
}





